import Editor from "./Editor";
import React, { Component } from "react";
import {
  addPage,
  selectPage,
  updatePage,
  state,
  getCurrentId
} from "./state/appState";
import StateSaver from './StateSaver';
import { EditorState, convertToRaw, convertFromRaw } from "draft-js";
export default class extends Component {

  getPreviewText(item, state) {
    let raw = state.pages[item];
    let previewText = '';
    if (raw) {
      try {
        let state = EditorState.createWithContent(convertFromRaw(raw));
        previewText = state.getCurrentContent().getPlainText().substr(0, 20);
      } catch(e) {
        console.error('there was an error', e);
      }
    }
    
    if (!previewText) {
      previewText =  <i>empty page</i>
    }

    return previewText;
  }

  render() {
    return (
      <div className="flex-grow">
        {state.get(state => (
          <React.Fragment>
            <div className="side-bar">
              {state.sideBarOrder.map((item, index) => (
                <div
                  className="saved-item"
                  style={{
                    backgroundColor:
                      index === state.selectedIndex ? "#bfdbff9e" : "white"
                  }}
                  onClick={() => selectPage(index)}
                >
                  {this.getPreviewText(item, state)}
                </div>
              ))}
              <div className="add-btn saved-item" onClick={addPage}>
                Add Page +
              </div>
            </div>
            {getCurrentId(id => (<Editor
                key={id}
                id={id}
                initialState={state.pages[id]}
                onChange={raw => {
                  updatePage(id, raw);
                }}
              />)
            )}
            <StateSaver />
          </React.Fragment>
        ))}
      </div>
    );
  }
}



