import Editor from "./Editor";
import React, { Component } from "react";
import {
  addPage,
  state,
  localStorageId
} from "./state/appState";

export default class extends Component {
  render() {
    return (
      <div style={{display: 'none'}}>
      {state.get(state => {
          window.localStorage.setItem(localStorageId, JSON.stringify(state));
          return (<div/>)
      })}
      </div>
    );
  }
}



