
import {create} from './reworm';
import produce from 'immer'

export const localStorageId = 'meow';
let initialState = JSON.parse(window.localStorage.getItem(localStorageId) || `{
    "pages":{},
    "sideBarOrder": ["default-page"],
    "selectedIndex": 0
}`);

console.log('initital state?', initialState);
export const state = create(initialState);


export const getCurrentId = state.select(state => {
    return state.sideBarOrder[state.selectedIndex];
})

export function addPage() {
    console.log('adding page');
    let id = getRandomId(10);
    state.set(
        produce(state => {
            state.sideBarOrder.push(id);
            selectPage(state.sideBarOrder.length - 1);
        })
    )

}

export function selectPage(index) {
    console.log()
    state.set(
        produce(state => {
            state.selectedIndex = index;
        })
    );
}

export function updatePage(id, update) {
    console.log('update page!!!!!!!');
    state.set(
        produce(state => {
            state.pages[id] = update;
        })
    )
}



function getRandomId(length) {
    // 
    var chars = "abcdefghijklmnopqrstuvwxyz0123456789";
    var charsLength = chars.length;
    var randomId = '';
    
    // 20
    if (!length) {
      length = 20;
    }
  
    for(var i=0; i<length; i++) {
      randomId += chars[Math.floor(Math.random() * charsLength)];
    }
  
    return randomId;
  }


